/*
*    Navigation
*/

a{
  text-decoration: none;
  font-weight: bold;
  padding: 0 0 0.2rem 0;
}
.backColor{
  background: linear-gradient(to right , #1056a5, rgba(0, 165, 122, 0.62)) !important;
}
p{
  color: #030506 !important;
}
.Features p{
  color: #2e4e5c;
}
.Features h1 , h3{
  color: #2e4e5c !important;
}
.navigationResponsive {
  display:-webkit-flex;
  display:flex;
  -webkit-flex-direction:column;
          flex-direction:column;
  -webkit-justify-content:space-between;
          justify-content:space-between;
