}

.navigation .logo {
  background-color: #333;
}

.navigation a  {
   color:white;


  font-size: larger;
 }
.navigation span  {
   color:white;
  font-size: larger;
  text-decoration: none;
 }

.navigation a:hover {
  opacity: .8;
}

.navigation .nav__right , .navigation .nav__left {
  display: -webkit-flex;
  display: flex;
}

.navigation .nav__right li , .navigation .nav__left li {
