/*
 *  Comment Section
 */

.comment-section {
  display: -webkit-flex;
  display: flex;

  background-color: #f5f5f5;
}

.comment-section__head {
  background: url(/static/media/charlotte-karlsen-585717-unsplash.316eb06e.jpg) no-repeat 50%;
  background-size: cover;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
  color: white;
  -webkit-flex-basis: 40%;
          flex-basis: 40%;
  position: relative;
}

.comment-section__head::after {
  content: ' ';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(0,0,0,.4);
}

.comment-section__head .content {
  z-index: 1;
  padding-right: 2%;
  padding-left: 2%;
  line-height: 1.8;
}

.comment-section__head .content h3 {
  font-weight: 400;
  font-size: 2.2em;
}

.comment-section__head .content p {
  font-size: 1em;
  margin-top: 10px;
  margin-bottom: 10px;
}

.comment-section__head .content a {
  font-size: 1.2em;
  border-bottom: 1px solid;
  color: white;
}

.comment-section__content {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-basis: 60%;
          flex-basis: 60%;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;

}

.comment-box {
  background-color: white;
  border: 1px solid rgba(0,0,0,0.1);
  -webkit-flex-basis: calc(40%);
          flex-basis: calc(40%);
  margin: 10px;
  padding: 20px;
}

.comment-box hr {
  border: 1px solid rgba(0,0,0,0.1);
  margin-top: 15px;
  margin-bottom: 15px;
}

.comment-box .comment-box__title {
  color: #333;
  font-size: 1.3em;
}

.comment-box .comment-box__description {
  line-height: 1.5;
}

.white {
  color: whitesmoke !important;
}
@media only screen and (max-width : 767px ) {

  .comment-section {
    -webkit-flex-direction: column;
            flex-direction: column;
  }

  .comment-section__head .content {
    margin: 15px;
  }


}

@media only screen and (max-width : 500px ) {



  .comment-box {
    -webkit-flex-basis: calc(100% - 20px);
            flex-basis: calc(100% - 20px);
  }
}

